import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { differenceInCalendarDays } from 'date-fns';
import { VhType } from 'src/app/sales/interface/vh-type';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhSaleShopeeInvoiceAdd, VhSaleShopeeInvoiceDetail, VhSaleShopeeInvoiceDetailCombo } from 'src/app/sales/components/bill-type/bill-type-1/shopee/shopee-interface';
@Component({
  selector: 'app-issue-delivery-goods',
  templateUrl: './issue-delivery-goods.component.html',
  styleUrls: ['./issue-delivery-goods.component.scss']
})
export class IssueDeliveryGoodsComponent implements OnInit {
  searchValue: any;
  searchProductAppValue: any = '';
  salesOrder: Array<any> = [];
  salesSubOrder: Array<any> = [];
  heightScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  dataRestore: any = {};
  bill_selected: any = {};
  list_status_bill: Array<any> = [
    { name: "All", value: "all" },
    { name: "Wait for confirmation", value: "UNPAID", value2: 'TO_CONFIRM_RECEIVE' },
    { name: "Ch ly hng", value: "READY_TO_SHIP" },
    { name: "Shipping", value: "PROCESSED" },
    { name: "Delivered", value: "SHIPPED" },
    { name: "Hon tt", value: "COMPLETED" },
    { name: "Cancellation", value: "CANCELLED" },
  ];
  /**Gii hn thi gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true;
    return false;
  };
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30);
  infoShop: any = {};
  listProductOfBranch: Array<any> = []; // sp ca phn mm c lin kt vi sp shopee
  listProductApp: Array<any> = []; // sp trong phn mm cha shopee[]
  listSubProductApp: Array<any> = []; //sp ca app  search
  listProductShopee: Array<any> = []; // sp ca shopee
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id;
  setHeight = { "max-height": "500px", "height": "500px", "overflow-y": "hidden" };
  invoice: VhSaleShopeeInvoiceAdd = {
    bill_type: 1,
    cqt_status: 1,
    payment_type: 2,
    id_customer: '',
    id_branch: '',
    id_employee: '',
    id_wallet: 'id_cash',
    date: '',
    total: 0,
    payment: 0,
    tax: 0,
    discount: 0,
    fee: 0,
    note: '',
    price_type: 0,
    order_number_ecommerce: '',
    ecommerce_fee_paid_by_seller:0,
    id_shop: '',
    tax_buyerDisplayName: '',
    tax_buyerLegalName: '',
    tax_buyerTaxCode: '',
    tax_buyerAddressLine: '',
    tax_buyerEmail: ''
  };
  invoiceDetail = [];
  employee: any = {};
  customer: any = {};
  subTotal = 0;

  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private vhEcommerce: VhEcommerce,
    private vhAuth: VhAuth,
    private nzModalService: NzModalService,
  ) {
    this.date[0].setHours(0, 0, 0, 0);
    this.date[1].setHours(23, 59, 59, 59);
    this.dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore || {};
  }
  ngOnInit(): void {
    this.initState();
  }

  initState() {
    if (this.dataRestore.id_shop_selected) {
      this.date = this.dataRestore.date;
      this.searchValue = this.dataRestore.searchValue;
      this.salesOrder = this.dataRestore.salesOrder;
      this.salesSubOrder = this.salesOrder;
      if (this.dataRestore['update']) {
        this.dataRestore['update'] = false;
        this.showReport();
      }
      else
        this.load_status = true;
    }
    this.infoShop = this.vhEcommerce.getlocalShopListShopee()[0];
    this.getMinDate();

    this.listProductOfBranch = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].shopees && item[this.id_branch].shopees.length >= 0);
    let localProducts: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden)

    this.listProductApp = localProducts.map(item => {
      let unit = this.vhQuerySales.getUnit_byRatio(item.units, 1);
      if (item[this.id_branch] && item[this.id_branch].shopees && item[this.id_branch].shopees.length) { //filter nhng sp shopee thuc id shopee  chn
        item[this.id_branch].product_shopees = [...item[this.id_branch].shopees.filter(prod => prod.id_shop == this.infoShop._id)];
      }
      return { ...item, ...unit };
    });
    this.listSubProductApp = [...this.listProductApp];

    this.listProductShopee = this.clearProductShow(this.vhEcommerce.getlocalProductListShopee(this.infoShop['_id']).filter(item => item.item_status == 'NORMAL'), this.listProductApp);
  }


  /**
   * Ly ngy gii hn c xem bo co
  */
  getMinDate() {
    let date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 100);
    this.vhQuerySales.getValidityDate(date).then((validitydate: Date) => {
      this.minDate = validitydate;
      if ((this.minDate.getTime()) < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
        this.disabledDate = (current: Date): boolean => {
          if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true;
          return false;
        };
      }
    });
  }
  /**Tm kim ha n */
  search(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
    this.salesOrder = this.vhAlgorithm.searchList(val, [...this.salesSubOrder], ['order_sn']);
  }

  /**
   * search tn sn phm trong app viethas
   * @example this.search('Sn phm A')
   */
  searchProductApp(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
    this.listProductApp = this.vhAlgorithm.searchList(val, [...this.listSubProductApp],  ['name','barcode','item_code']);
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/issue-invoice'], {
      state: { dataRestore: this.dataRestore }
    });
  }
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 1209600000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("Thi gian xem khng qu 15 ngy"), "alert-toast");
      this.date = [event[0], new Date(event[0].getTime() + 1209600000)];
    }
  }
  showReport() {
    this.load_status = true;
    this.salesOrder = [];
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhEcommerce.getSalesOrderListShopee(new Date(this.datePipe.transform(new Date(this.date[0].setHours(0, 0, 0)), "yyyy-MM-dd HH:mm:ss").toString()), new Date(this.datePipe.transform(new Date(this.date[1].setHours(23, 59, 59)), "yyyy-MM-dd HH:mm:ss").toString()), this.infoShop.access_token.toString(), this.infoShop._id.toString())
        .then((result: any) => {
          this.vhComponent.hideLoading(0);
          result.forEach(item => {
            if (item.verify_status_delivered_goods)
              this.salesOrder.push({ ...item, create_at: new Date(item.create_time * 1000) });
          });
          this.salesSubOrder = [...this.salesOrder];
        })
        .catch(err => this.vhComponent.hideLoading(0));
    });
  }
  changeShop(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#shopee-issue-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".shopee-issue-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#shopee-issue-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".shopee-issue-invoice-header").clientHeight - 90 + "px";
    }

    this.cdRef.detectChanges();
  }
  gotoCreateBill(bill: any) {
    //tm customer  loi tr bug khch hng b xa ri
    let customer: any = this.vhQuerySales.getlocalCustomer(this.infoShop.data?.id_customer);
    if (customer) {
      this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/issue-invoice/issue-delivery-goods/add'], {
        state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, billShopee: bill }
      });
    } else this.vhComponent.alertMessageDesktop('warning', this.lang.translate('Please update customer in settings'));
  }
  gotoDetail(bill: any) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/issue-invoice/issue-delivery-goods/detail'], {
      state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, id: bill.id_bill_delivered_goods, billShopee: bill }
    });
  }
  gotoDetailShopee(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/issue-invoice/issue-delivery-goods/detail-shopee'],
      {
        state: {
          bill: bill,
          dataRestore: {
            salesOrder: this.salesOrder, searchValue: this.searchValue,
            id_shop_selected: this.infoShop._id, listProduct: this.dataRestore.listProduct,
            date: this.date,
            salesSubOrder: this.salesSubOrder,
          }
        }
      });
  }

  findProductLinked(item) {
    for (let productOfBranch of this.listProductOfBranch) {
      const shopees = productOfBranch[this.id_branch]?.shopees || [];

      const found = shopees.find(data => {
        if (data.item_id !== item.item_id) return false;

        if (data.model?.length && item.model_id !== 0) {
          return data.model.some(model => model.model_id === item.model_id);
        }

        return data.model?.length ? false : true;
      });

      if (found) {
        return productOfBranch;
      }
    }

    return null;
  }

  /**
   * Kim tra sn phm  c lien kt hay cha
   *
   */
  isLinkedToProduct(item): boolean {
    for (let productOfBranch of this.listProductOfBranch) {
      let findProduct = productOfBranch[this.id_branch].shopees.find(data => data.item_id == item.item_id);
      if (findProduct) {
        if (findProduct.model && findProduct.model.length && item.model_id != 0) {
          let indexModel = findProduct.model.findIndex(model => model.model_id == item.model_id);
          if (indexModel >= 0) {
            return true;
          }
        } else {
          return true;
        }
      }
    };
    return false;
  }

  toggleSelectAll(checked: boolean) {
    this.checkAll = checked;
    this.listInvoiceSelected = []; // reset before adding

    for (let bill of this.salesOrder) {
      const canSelect = !bill.id_bill_delivered_goods && this.billIsLinkedToProduct(bill);

      if (canSelect) {
        this.selectedBills[bill.order_sn] = checked;

        if (checked) {
          this.listInvoiceSelected.push(bill);
        }
      } else {
        // ensure unselect disabled ones
        this.selectedBills[bill.order_sn] = false;
      }
    }
  }


  billIsLinkedToProduct(bill) {
    return bill.item_list.every(item => this.isLinkedToProduct(item));
  }


  checkAll: boolean = false;
  selectedBills: { [id: string]: boolean; } = {};
  listInvoiceSelected = [];

  checkBillToCreateInvoice(bool, bill) {
    this.selectedBills[bill.order_sn] = bool;
    if (bool) {
      this.listInvoiceSelected.push(bill);
    }
    else {
      this.listInvoiceSelected = this.listInvoiceSelected.filter(item => item.order_sn != bill.order_sn);
    }

    const allBillsCanSelect = this.salesOrder.filter(
      b => !b.id_bill_delivered_goods && this.billIsLinkedToProduct(b)
    );

    this.checkAll = allBillsCanSelect.every(b => this.selectedBills[b.order_sn]);
  }

  visible_affiliate: boolean = false;
  productShopeeSelected: any = null;

  openModelSelectProductsShopee(productShopee) {
    this.productShopeeSelected = productShopee;
    this.visible_affiliate = true;
  }

  /**
   * Tt modal v clone mng product ra mt mng mi  trnh nh li product c b checked
   */
  cancelAffiliate() {
    this.visible_affiliate = false;
  }

  /**
   * Lin kt sn phm t shopee vi sn phm ti ca hng.
   * @param product Sn phm ca ca hng
   */
  acceptAfiliate(product) {
    const isZeroPriceCombo = product.type === VhType.COMBO && product.units[0].price === 0;
    const confirmZeroPriceComboMessage = this.lang.translate("This product is a combo with a price of 0. Do you still want to link it with the product", {platform : 'Shopee'}) + "?";
    this.nzModalService.confirm({
      nzTitle: '<i>' + (isZeroPriceCombo ? confirmZeroPriceComboMessage : this.lang.translate("Confirm")) + '</i>',
      nzIconType: "info-circle",
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          // Tm sn phm shopee
          const currentProductShopee = this.listProductShopee.find(item => item.item_id == this.productShopeeSelected.item_id);
          const id_product_selected = product.id_subproduct ? product.id_subproduct : product['_id'];

          if (!currentProductShopee) {
            this.vhComponent.hideLoading().then(() => {
              this.vhComponent.alertMessageDesktop('error', this.lang.translate("The product link failed"));
            });
            return;
          }

          let productShopee = {
            item_id: this.productShopeeSelected.item_id,
            models: []
          };

          // Nu sn phm shopee c model th thm vo mng
          if (currentProductShopee && currentProductShopee.has_model) {
            const model = currentProductShopee.model.find(item => item.model_id == this.productShopeeSelected.model_id);
            productShopee.models.push(model);
          }

          this.vhEcommerce.addMergeShopeesProduct_model(this.vhQuerySales.getlocalDetailProduct(id_product_selected), productShopee.item_id, productShopee.models, this.infoShop['_id'], this.id_branch).then((bool) => {
            this.vhComponent.hideLoading().then(() => {
              setTimeout(() => {
                this.visible_affiliate = false;
                this.listProductOfBranch = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].shopees && item[this.id_branch].shopees.length >= 0);
                this.vhComponent.alertMessageDesktop('success', this.lang.translate("Product linked successfully"));
              }, 300);
            });
          }).catch(err => {
            console.error(err);
            this.visible_affiliate = false;
            this.vhComponent.alertMessageDesktop('error', this.lang.translate("The product link failed"));
            this.vhComponent.hideLoading(0);
          });
        });
      }
    });
  }

  /**Xa nhng sp  c lin kt ri ra khi mng ca modal danh sch sp lin kt
   * @example this.clearProductShow(listProductShopee, listProductApp)
   */
  clearProductShow(listProduct, listProductApp) {
    for (let prodApp of listProductApp) {
      if (prodApp[this.id_branch] && prodApp[this.id_branch].shopees && prodApp[this.id_branch].shopees.length) {
        let arrayProdShopee = prodApp[this.id_branch].shopees;
        for (let item of arrayProdShopee) {
          let indexProd = listProduct.findIndex(prod => prod.item_id == item.item_id);
          if (indexProd >= 0) {
            if (listProduct[indexProd].model && item.model && item.model.length) {
              for (let model of item.model) {
                let index = listProduct[indexProd].model.findIndex(data => data.model_id == model?.model_id);
                if (index >= 0) listProduct[indexProd].model.splice(index, 1);
              }
              if (!listProduct[indexProd].model.length) listProduct.splice(indexProd, 1);
            } else listProduct.splice(indexProd, 1);
          }
        }
      }
    }
    return listProduct;
  }

  payInvoice() {
    this.vhComponent.alertConfirm(
      `${this.lang.translate("Pay")}?`,
      "", "", "OK", this.lang.translate("Cancel")
    )
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(async () => {
          const updatePromises = [];

          for (let index = 0; index < this.listInvoiceSelected.length; index++) {
            const billShopee = this.listInvoiceSelected[index];
            await this.initBill(billShopee);

            const promise = this.createOrUpdate(1).then((bill: any) => {
              this.salesOrder.find(item => item.order_sn == billShopee['order_sn']).id_bill_delivered_goods = bill._id;
              if (this.invoice.cqt_status !== 1) {
                this.vhQuerySales.updateBill(bill._id, { cqt_status: this.invoice.cqt_status });
              }
              return this.vhEcommerce.updateSalesOrderShopee(this.infoShop._id, billShopee['order_sn'].toString(), { id_bill_delivered_goods: bill._id });
            });

            updatePromises.push(promise.catch(err => {
              this.vhComponent.showToast(2000, err);
              return null;
            }));
          }

          // Wait for all promises to resolve (or reject caught above)
          Promise.all(updatePromises).then(() => {
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(2000,
                `${this.lang.translate("Issue invoice")} ${this.lang.translate("Successfully").toLocaleLowerCase()}`, "success-toast"
              );
            });
          });
        });
      }, () => { });

    // }
    // else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  createOrUpdate(bill_type: 1) {
    return new Promise((resolve, reject) => {
      this.vhQuerySales.createBill_Billdetail({ ...this.invoice }, this.getCreateUpdateInvoiceDetail()).then(res => resolve(res)).catch(err => reject(err));
    });
  }

  /**
   * Thm sn phm  bn vo cart
   * Sn phm ny l sp  bn trn sn
   */
  addInvoiceDetail(item_list: Array<any>) {
    let products: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].shopees && item[this.id_branch].shopees.length >= 0);
    for (let item of item_list) {
      let findSuccess: boolean = false;
      for (let productOfBranch of products) {
        let findProduct = productOfBranch[this.id_branch].shopees.find(data => data.item_id == item.item_id);
        if (findProduct) {

          if (findProduct.model && findProduct.model.length && item.model_id != 0) {
            let indexModel = findProduct.model.findIndex(model => model.model_id == item.model_id);
            if (indexModel >= 0) {
              this.addInvoiceDetailShopee(item.model_discounted_price, item.model_original_price, item.model_quantity_purchased, productOfBranch, true);
              findSuccess = true;
              break;
            }
          } else {
            this.addInvoiceDetailShopee(item.model_discounted_price, item.model_original_price, item.model_quantity_purchased, productOfBranch, true);
            findSuccess = true;
            break;
          }
        }
      }
      if (!findSuccess) {
        this.addInvoiceDetailShopee(item.model_discounted_price, item.model_original_price, item.model_quantity_purchased, { name: item.item_name }, false);
      }
    }
  }


  /**
   *
   * @param price
   * @param product
   * @param exist
   * @example addInvoiceDetailShopee(price_on_shopee, product, false)
   */
  addInvoiceDetailShopee(price, price_origin, quantity, product, exist) {
    if (exist) {
      const productToAdd = {
        name: product.name,
        id_product: product._id,
        quantity: quantity,
        ptype: product.type,
        unit: product.unit,
        ratio: 1,
        units: product.units,
        price: price,
        price_origin: price_origin || 0,
        id_subproduct: product.id_subproduct,
        lots: product.lots,
        id_lotproduct: product.id_lotproduct,
        free: false, //nu c trng ny true th c quyn chnh s lng
      }
      if(product.type == VhType.COMBO) {
        productToAdd['combos'] = product.combos;
      }
      this.invoiceDetail.unshift(productToAdd);
    } else {
      this.invoiceDetail.unshift({
        id_product: null,
        quantity: quantity,
        price: price,
        ptype: 1,
        ratio: 1,
        name: product.name
      });
    }
  }

  /**
     * tr v danh sch bill_detail  format theo ng chun cc trng  lu DB
     * @returns array bill_detail
     * @example let bill_details = getCreateUpdateInvoiceDetail()
     */
  getCreateUpdateInvoiceDetail(): VhSaleShopeeInvoiceDetail[] {
    return this.invoiceDetail.map(item => {

      let detail: VhSaleShopeeInvoiceDetail = {
        id_product: item.id_product,
        ptype: item.ptype,
        price: item.price,
        quantity: item.quantity,
        ratio: item.ratio,
        price_origin: item.price_origin,
      };
      if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct;
      if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct;

      if (item.ptype == 5) {
        detail['combos'] = item.combos.map(combo => {
          let product: VhSaleShopeeInvoiceDetailCombo = {
            id_product: combo.id_product,
            price: combo.price,
            quantity: combo.quantity,
            ptype: combo.ptype || combo.type,
            ratio: combo.ratio ? combo.ratio : 1,
          };
          if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct;
          if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct;
          return product;
        });
      }
      return detail;

    }).filter(item => item);

  }
  pickerTime = new Date();

  async initBill(billShopee) {
    this.resetInvoice();
    const escrowShopee: any = await this.vhEcommerce.getEscrowShopee(billShopee.order_sn, this.infoShop.access_token, this.infoShop._id)
    console.log('escrowShopee', escrowShopee);
    this.invoice.date = this.pickerTime.toISOString();
    this.invoice.id_employee = this.vhAuth.getUser()._id;
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    this.invoice.id_branch = (this.vhQuerySales.getDefaultBranch()._id);
    this.invoice.id_customer = (this.vhEcommerce.getlocalShopListShopee().find(item => item._id == this.infoShop._id).data.id_customer);
    this.customer = this.vhQuerySales.getlocalCustomer(this.invoice.id_customer);
    this.invoice.payment_type = (2); //set bill cng n
    this.addInvoiceDetail(billShopee.item_list);

    this.invoice.discount = escrowShopee.order_income.voucher_from_seller || 0;
    this.invoice.fee = escrowShopee.order_income.actual_shipping_fee - escrowShopee.order_income.shopee_shipping_rebate;
    this.invoice.ecommerce_fee_paid_by_seller = escrowShopee.order_income.seller_transaction_fee + escrowShopee.order_income.shipping_seller_protection_fee_amount + escrowShopee.order_income.commission_fee;
    this.invoice.order_number_ecommerce = (billShopee['order_sn'].toString());
    this.invoice.id_shop = (this.infoShop._id);
    this.invoice.note = (`${this.lang.translate("Order number")} Shopee ${billShopee.order_sn}`);
    this.invoice.tax_buyerDisplayName = this.invoice.tax_buyerDisplayName ? this.invoice.tax_buyerDisplayName : 'Ngi mua khng ly ha n';
    this.invoice.cqt_status = billShopee.cqt_status ? billShopee.cqt_status : 1;
    this.setSubTotal();
  }

  /**
   * tnh v gn li cng tin (subTotal) ca n
   * @example this.setSubTotal()
   */
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoiceDetail) {
      subTotal += (i.price ? i.price * i.quantity : 0);
    }
    this.subTotal = subTotal;
    this.setTotal();
  }

  /**
   * tnh v gn li tng tin (total) ca n
   * @example this.setTotal()
   */
  setTotal() {
    this.invoice.total = this.subTotal - this.invoice.discount + this.invoice.fee > 0 ? this.subTotal - this.invoice.discount + this.invoice.fee : 0;
  }

  resetInvoice() {
    this.invoice = {
      bill_type: 1,
      cqt_status: 1,
      payment_type: 2,
      id_customer: '',
      id_branch: '',
      id_employee: '',
      ecommerce_fee_paid_by_seller: 0,
      id_wallet: 'id_cash',
      date: '',
      total: 0,
      payment: 0,
      tax: 0,
      discount: 0,
      fee: 0,
      note: '',
      price_type: 0,
      order_number_ecommerce: '',
      id_shop: '',
      tax_buyerDisplayName: '',
      tax_buyerLegalName: '',
      tax_buyerTaxCode: '',
      tax_buyerAddressLine: '',
      tax_buyerEmail: ''
    };
    this.invoiceDetail = [];
  }
  
  
  
  /**
   * m s lng bill ang check
   */
  get countSelectedBill(): number {
    let count = 0;
    for (let key in this.selectedBills) {
      if (this.selectedBills[key]) {
        count++;
      }
    }
    return count;
  }
  
}
